.more__title {
  position: absolute;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}

@media screen and (max-width: 768px) {
