@keyframes more-title {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(100%, 0);
  }
  51% {
    transform: translate(-100%, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

.more__title-overlay {
  position: absolute;
  overflow: hidden;
  transition: 1s;
}

@media screen and (max-width: 768px) {
  .more__title-overlay {
    top: 78px;
    right: -10px;
  }
}

