.experience {
  display: grid;
}

@media screen and (max-width: 768px) {
  .experience {
    margin: 0 0 120px 0;
    grid-template-columns: min-content 1fr;
    grid-template-rows: min-content min-content max-content min-content;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .experience {
    margin: 0 0 210px 0;
    grid-template-rows: min-content min-content 400px min-content;
    row-gap: 27px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .experience {
    grid-template-columns: 542px 1fr;
    grid-template-rows: repeat(3, min-content);
    column-gap: 52px;
    row-gap: 35px;
    margin: 0 38px 255px 38px;
  }
}

@media screen and (min-width: 1440px) {
  .experience {
    grid-template-columns: 542px 1fr;
    grid-template-rows: repeat(3, min-content);
    column-gap: 52px;
    row-gap: 35px;
    margin: 0 38px 255px 38px;
  }
}

.experience__title {
  align-self: start;
  justify-self: start;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: left;
  text-transform: uppercase;
  color: #B68F33; 
}

@media screen and (max-width: 768px) {
  .experience__title {
    grid-area: 1 / 2 / 2 / 3;
    margin: 29px 0 13px 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .experience__title {
    grid-area: 1 / 1 / 2 / 2;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .experience__title {
    grid-area: 1 / 1 / 2 / 3;
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .experience__title {
    grid-area: 1 / 1 / 2 / 3;
    margin-bottom: 32px;
  }
}

.experience__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width: 768px) {
  .experience__image {
    grid-area: 3 / 1 / 4 / 3;
    margin-bottom: 23px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .experience__image {
    grid-area: 3 / 1 / 4 / 2;
    margin-bottom: 13px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .experience__image {
    grid-area: 2 / 1 / 4 / 2;
  }
}

@media screen and (min-width: 1440px) {
  .experience__image {
    grid-area: 2 / 1 / 4 / 2;
  }
}



