.experience__texts-main {
  align-self: end;
  justify-self: end;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: rgba(255, 255, 255, 0.9);
}

@media screen and (max-width: 768px) {
  .experience__texts-main {
    grid-area: 2 / 2 / 3 / 3;
    margin: 0 12px 36px 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .experience__texts-main {
    grid-area: 2 / 1 / 3 / 2;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .experience__texts-main {
    grid-area: 2 / 2 / 3 / 3;

  }
}

@media screen and (min-width: 1440px) {
  .experience__texts-main {
    grid-area: 2 / 2 / 3 / 3;

  }
}

.experience__texts-column {
  display: grid;
}

@media screen and (max-width: 768px) {
  .experience__texts-column {
    grid-area: 5 / 1 / 6 / 3;
    margin: 0 12px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .experience__texts-column {
    grid-area: 4 / 1 / 5 / 2;
    grid-template-columns: 10fr 8fr;
    column-gap: 100px;
    padding-top: 24px;
    border-top: 1px solid rgba(255, 255, 255, 0.4);
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .experience__texts-column {
    grid-area: 3 / 2 / 4 / 3;
    grid-template-columns: 10fr 9fr;
    column-gap: 48px;
    padding-top: 16px;
    border-top: 1px solid rgba(255, 255, 255, 0.4);
  }
}

@media screen and (min-width: 1440px) {
  .experience__texts-column {
    grid-area: 3 / 2 / 4 / 3;
    grid-template-columns: 10fr 9fr;
    column-gap: 48px;
    padding-top: 16px;
    border-top: 1px solid rgba(255, 255, 255, 0.4);
  }
}

.experience__texts-additional {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.8);
}

@media screen and (max-width: 768px) {
  .experience__lines {
    grid-area: 1 / 1 / 3 / 2;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr max-content;
    column-gap: 10px;
  }
}

@media screen and (min-width: 768px) {
  .experience__lines {
    display: none;
  }
}

.experience__line_1 {
  width: 1px;
  height: 78%;
  background-color: #B68F33;
  align-self: end;
  margin-left: 12px;
}

.experience__line_2 {
  width: 1px;
  height: 65%;
  background-color: rgba(255, 255, 255, 0.8);
  align-self: start;
  margin-right: 30px;
}

