.footer {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 768px) {
  .footer {
    margin: 0 6px;
  }
}

.footer__title {
  width: 100%;
}

@media screen and (max-width: 768px) {
  .footer__title{
    margin-bottom: 15px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .footer__title{
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .footer__title{
    margin-bottom: 22px;
  }
}

@media screen and (min-width: 1440px) {
  .footer__title{
    margin-bottom: 22px;
  }
}


.footer__links {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 768px) {
  .footer__links {
    flex-direction: column;
    align-items: flex-start;
    order: -1;
    margin-bottom: 110px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .footer__links {
    margin: 0 22px 23px 22px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .footer__links {
    margin: 0 55px 32px 55px;
  }
}

@media screen and (min-width: 1440px) {
  .footer__links {
    margin: 0 55px 32px 55px;
  }
}

.footer__link {
  font-family: Audrey;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
  color: rgba(182, 143, 51, 0.8);
  padding: 4px;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .footer__link {
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .footer__link {
    font-size: 12px;
    line-height: 14px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .footer__link {
    font-size: 12px;
    line-height: 14px;
  }
}

@media screen and (min-width: 1440px) {
  .footer__link {
    font-size: 12px;
    line-height: 14px;
  }
}

