.footer__copyright {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
}

@media screen and (max-width: 768px) {
  .footer__copyright {
    margin-bottom: 11px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .footer__copyright {
    margin-bottom: 14px;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .footer__copyright {
    margin-bottom: 14px;
  }
}

@media screen and (min-width: 1440px) {
  .footer__copyright {
    margin-bottom: 14px;
  }
}