}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .clients__title {
    font-size: 28px;
    line-height: 34px;
    text-align: center;
    padding-bottom: 8px;
    margin-bottom: 63px;
    margin-right: auto;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .clients__title {
