.clients__logos {
  width: 100%;
  display: grid;
  grid-auto-flow: column;
  justify-items: center;
  align-items: center;
  /*display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;*/
}

@media screen and (max-width: 768px) {
  .clients__logos {
    grid-template-columns: repeat(9, 200px);
    grid-template-rows: repeat(4, 1fr);
    grid-auto-rows: 78px;
    margin-bottom: 180px;
    margin-left: 33px;
    padding-right: 200px;
    overflow-y: scroll;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .clients__logos {
    /*grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(9, 1fr);
    grid-auto-rows: 78px;

    margin-bottom: 80px;*/
    grid-template-columns: repeat(9, 200px);
    grid-template-rows: repeat(4, 1fr);
    grid-auto-rows: 78px;
    margin-bottom: 180px;
    margin-left: 33px;
    padding-right: 45px;
    overflow-y: scroll;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .clients__logos {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(6, 1fr);
    grid-auto-rows: 78px;
    margin-bottom: 80px;
  }
}

@media screen and (min-width: 1440px) {
  .clients__logos {
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(6, 1fr);
    grid-auto-rows: 78px;
    margin-bottom: 80px;
  }
}

.clients__logos-image {
  object-fit: cover;
  transition: 0.5s;
  justify-self: start;
}



@media screen and (max-width: 768px) {
  .clients__logos-image:nth-child(even) {
    justify-self: end;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px){
  .clients__logos-image:nth-child(odd) {
    justify-self: end;
  }
  .clients__logos-image:nth-child(-n + 27):nth-child(odd) {
    justify-self: start;
  }
  .clients__logos-image:nth-child(-n + 27):nth-child(even) {
    justify-self: end;
  }
  .clients__logos-image:nth-child(-n + 18):nth-child(odd) {
    justify-self: end;
  }
  .clients__logos-image:nth-child(-n + 18):nth-child(even) {
    justify-self: start;
  }
  .clients__logos-image:nth-child(-n + 9):nth-child(odd) {
    justify-self: start;
  }
  .clients__logos-image:nth-child(-n + 9):nth-child(even) {
    justify-self: end;
  }
}

@media screen and (min-width: 1280px) and (max-width: 1440px){
  .clients__logos-image:nth-child(even) {
    justify-self: end;
  }
}

@media screen and (min-width: 1440px) {
  .clients__logos-image:nth-child(even) {
    justify-self: end;
  }
}

/*.clients__logos-image:nth-child(n + 6) {
  justify-self: end;
}*/

.clients__logos-image:hover {
  opacity: 0.6;
  transform: scale(0.9, 0.9);
}

